import {combineReducers, createStore} from "redux";

let cartInfo = [];
let alertState = true;

function reducer(state=cartInfo , action){
    let copy = [...state];
    switch(action.type) {
        case '장바구니추가' :
            if(copy.length===0) {
                copy.push(action.payload);
                return copy;
            }else{
                const index=copy.findIndex((e) => e.id === action.payload.id);
                if (index === -1) {
                    copy.push(action.payload);
                    return copy;
                } else {
                    const index=copy.findIndex((e) => e.id === action.payload.id);
                    copy[index].quan++;
                    return copy;
                }
            }
        case '증가' : copy[action.payloadIndex].quan++; return copy;
        case '감소' : copy[action.payloadIndex].quan--; return copy;
        default : return state;
    }
}

function reducer2(state=alertState,action){
    if(action.type==='확인'){
        return state=false;
    }else{
        return state;
    }
}

let store = createStore(combineReducers({reducer,reducer2}));

export {store};