import {Table} from "react-bootstrap";
import {connect, useDispatch, useSelector} from "react-redux";
import { Button } from "react-bootstrap"
import Alert from "./Alert";

function Cart(props) {
    let cartState = useSelector((state) => state.reducer);
    let alertState = useSelector((state) => state.reducer2);
    let dispatch = useDispatch();
    return (
        <>
            <Table striped bordered hover>
                <thead>
                <tr>
                    <th>#</th>
                    <th>상품명</th>
                    <th>수량</th>
                    <th>변경</th>
                </tr>
                </thead>
                <tbody>
                { cartState ?
                    cartState.map((e,i) => {
                        return (
                            <tr key={e.id}>
                                        <td>{i+1}. <img src={e.src} width='100' height='120'/></td>
                                        <td style={{verticalAlign:"middle"}}>{e.title}</td>
                                        <td style={{verticalAlign:"middle"}}>{e.quan}</td>
                                        <td style={{verticalAlign:"middle"}}>
                                            <Button variant="primary" size="sm" onClick={()=>{dispatch({type:'증가',payloadIndex:i})}} >+</Button>
                                            <Button variant="secondary" size="sm" onClick={()=>{dispatch({type:'감소',payloadIndex:i})}}>-</Button>
                                </td>
                            </tr>
                        )
                    }) : null
                }
                </tbody>
            </Table>
            { alertState ? <Alert/> :null }
        </>
    )
}

/*function stateToProps(state) {
    return {
        cart: state.reducer,
        alertState : state.reducer2
    }

}*/

/*
export default connect(stateToProps)(Cart);*/

export default Cart;