import './App.css';
import ShopNavbar from "./navigation/ShopNavbar";
import React, {useState, lazy, Suspense} from "react";
//import ItemsDetail from "./view/ItemsDetail"
import Spinner from 'react-bootstrap/Spinner'
import Main from "./view/Main";
import Items from "./view/Items";
import {Route} from "react-router-dom";
import ItemData from "./view/ItemListDetail";
import Cart from "./view/useablecomponent/Cart";
import AgeAsyncTest from "./AgeAsyncTest";

let ItemDetail = lazy(() => {
    return import("./view/ItemsDetail");
});

export let StockContext = React.createContext();

function App() {
    let [mainSwitch, setMainSwitch] = useState(true);
    const [itemList, setItemList] = useState(ItemData);
    let [stock, setStock] = useState([10, 20, 30]);
    return (

        <div className="App">
            <ShopNavbar setMainSwitch={setMainSwitch}/>
            <StockContext.Provider value={stock}>
                <Route exact path="/">
                    {mainSwitch ? <Main setMainSwitch={setMainSwitch}/> : null}
                    <Items itemList={itemList} setItemList={setItemList}/>
                </Route>

                <Route exact path="/detail/:id">
                    <Suspense fallback={<Spinner animation="border" />}>
                        <ItemDetail itemList={itemList}/>
                    </Suspense>
                </Route>

                <Route exact path="/detail">
                    <div>This site is HooShop</div>
                    <AgeAsyncTest/>
                </Route>

                <Route exact path="/cart" component={Cart}/>

                <Route exact path="/companyInfo">
                    <br/><br/><br/>
                    <h1>도와주십쇼</h1>
                </Route>

            </StockContext.Provider>
        </div>
    );
}

export default App;
