import {useEffect, useState} from "react";
import {Alert, Button} from "react-bootstrap";
import {connect} from "react-redux";

function AlertPage(props) {
    return (
        <>
                <Alert variant="secondary">
                    <Alert.Heading>이 제품은 어떠신가요?</Alert.Heading>
                    <p>
                        지금 구매하면 25% 할인이요
                    </p>
                    <hr />
                    <div className="d-flex justify-content-end">
                        <Button onClick={() => {props.dispatch({type:'확인'})}} variant="outline-success">
                            확인
                        </Button>
                    </div>
                </Alert>
        </>
    );
}

function toProp(state){
    return {
        show : state.reducer2
    }
}

export default connect(toProp)(AlertPage);