import {Container, Nav, Navbar, NavDropdown} from "react-bootstrap";
import {Link,useHistory} from "react-router-dom";

function ShopNavbar(props){
    let history = useHistory();
    return(
        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
            <Container>
                <Navbar.Brand style={{cursor:"pointer"}} onClick={()=>{history.push("/"); props.setMainSwitch(true)}} >Hoo shop</Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link as={Link} to="/detail">Site Info</Nav.Link>
                    </Nav>
                    <Nav>
                        <NavDropdown title="더 보기" id="collasible-nav-dropdown">
                            <NavDropdown.Item as={Link} to="/cart" >장바구니</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item as={Link} to="/companyInfo" >회사정보</NavDropdown.Item>
                        </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}

export default ShopNavbar;