import {useEffect, useState} from "react";

function AgeAsyncTest(){
        let [count, setCount] = useState(0);
        let [age, setAge] = useState(20);
        useEffect(()=>{
            //page최초 렌더링시에 useEffect가 실행이된다.
            //그걸 막기위해서 count!==0 이라는 조건을 더 달았다.
            if(count!==0 && count<3)
                setAge(age + 1);
            else
                console.log('stop')
        },[count])
    /*
        function ageUpTest(){
            setCount(count+1);
            if(count<3){
                setAge(age+1);
            }else{
                console.log('stop');
            }
        }*/
        return (
            <div>
                <div>안녕하십니까 전 {age} 카운트가 3이상이면 더이상 나이가 안오릅니다. 카운트 : {count}</div>
                <button onClick={()=>{setCount(count+1)}}>누르면한살먹기</button>
            </div>
        )
    }

export default AgeAsyncTest;